import React, { useState, useEffect } from "react";
import classnames from "classnames";
import Header from "../partials/Header";
import Sidebar from "../partials/Sidebar";
import BoardNav from "../partials/BoardNav";
import QuillEditor from "../partials/EditorComponent";
import Select from "react-select";
import { useHistory } from "react-router";
function Write() {
    const history = useHistory();
    const [title, setTitle] = useState('');
    const [content, setContent] = useState('');
    const [category, setCategory] = useState({value: 0, label: ''});
    const [options, setOptions] = useState();

    useEffect(async () => {
        if(!localStorage.getItem("token")) {
            alert(" ");
            history.push("/");
        }else {
            const result = await fetch("/api/board/options", {
                headers: {
                    Authorization: `Bearer ${localStorage.getItem("token")}`
                }
            }).then(res => res.json());

            setOptions(result.option);
        }
    }, []);

    const onChangeTitle = (e) => {
        setTitle(e.target.value);
    }
    const editorChange = (value) => {
        setContent(value);
    }

    const categoryChange = (selectedOption) => {
        console.log(category.value);
        setCategory(selectedOption);
    }

    const onBackButtonClick = () => {
        history.goBack();
    }
    
    const onPostButtonClick = async () => {
        if(category.value === 0) {
            alert('  .');
            return;
        }

        if(!title) {
            alert(" .");
            return;
        }

        if(!content) {
            alert(" .");
            return;
        }
        const result = await fetch("/api/board/write", {
            headers: {
                "Content-Type": "application/json",
                Authorization: `Bearer ${localStorage.getItem("token")}`
            },
            method: "POST",
            body: JSON.stringify({
                board_type: category.value,
                title: title,
                contents: content
            })
        }).then(response => response.json());

        if(result.success) {
            alert(" .");
            history.goBack();
        }else {
            alert(" .");
            return;
        }

        console.log(result);
    }



    const navList = [" "];
    const navURL = ["/board/write"];
    const sidebarList = ["", " ", " ", ""];
    const sidebarLinks = ["/manage", "/productList", "/template/delivery", "/board/notice"];
    return (
        <div className="container">
            <Header type="boardList"></Header>
            <div className="side__wrapper">
                <Sidebar sidebarList={sidebarList} selectedItem="boards" sidebarLinks={sidebarLinks}></Sidebar>
                <BoardNav selectedNav=" " navList={navList} navURL={navURL} single="true">
                    <div className="category" style={{width: "200px", marginLeft: "15px", marginBottom: "15px", marginRight: "15px"}}>                        
                            <Select value={category} onChange={categoryChange} options={options} placeholder="" style={{marginRight: '30px'}}/>
                            
                    </div>
                    <div className="wrapper__editor">
                        <textarea className="editor__title--input" placeholder=" " value={title} onChange={onChangeTitle}></textarea>
                    </div>
                    <QuillEditor value={content} onChange={editorChange}/>
                    
                    <div className="editor__footer" style={{textAlign: "end", paddingRight: "5px", paddingBottom: "40px"}}>
                        <button className="lists__processButton lists__createButton" onClick={onBackButtonClick}></button>
                        <button className="lists__processButton lists__createButton" onClick={onPostButtonClick}></button>
                    </div>
                </BoardNav>
            </div>
        </div>
    )
}

export default Write;