import React, { useState , useEffect} from "react";
import classnames from "classnames";
import Header from "../partials/Header";
import Sidebar from "../partials/Sidebar";
import BoardList from "../partials/BoardList";

function IssueList() {
    const [issueList, setIssueList] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [postsPerPage, setPostsPerPage] = useState(10);

    useEffect(async() => {
        const issueList = await fetch("/api/board/issue", {
            method: "GET"
        }).then(result => result.json());
        setIssueList(issueList.issueList);
    }, []);

    let indexOfLast = currentPage * postsPerPage;
    let indexOfFirst = indexOfLast - postsPerPage;
    const currentPosts = issueList.slice(indexOfFirst, indexOfLast);


    const navList = ["", "/", " ", "  "];
    const navURL = ["/board/notice", "/board/question", "/board/issue", "/board/request"];
    const sidebarList = ["", " ", " ", ""];
    const sidebarLinks = ["/manage", "/productList", "/template/delivery", "/board/notice"];
    return (
        <div className="container">
            <Header type="boardList"></Header>
            <div className="side__wrapper">
                <Sidebar sidebarList={sidebarList} selectedItem="boards" sidebarLinks={sidebarLinks}></Sidebar>
                <BoardList selectedNav=" " navList={navList} navURL={navURL} postsPerPage={postsPerPage} boardData={currentPosts} paginate={setCurrentPage} totalPosts={issueList.length} currentPage={currentPage}></BoardList>
            </div>
        </div>
    )
}

export default IssueList;